import Vue from "vue"
import VueRouter from "vue-router"
import DropSchedule from "../components/DropList/DropSchedule"
import FullList from "../components/DropList/FullList"
import Home from "../components/HomePage/Home"
import RegularArticle from "../components/HomePage/RegularArticle"
import Comments from "../components/Comments"
import Brands from "../components/DropList/Brands"
import Shop from "../components/ShopPage/Shop"
import Tag from "../components/Tag/TagPage"
import Error from "../components/404Page/ErrorPage"

Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/articles/:id",
    name: "RegularArticle",
    component: RegularArticle,
  },
  {
    path: "/droplist",
    name: "Brands",
    component: Brands,
  },
  {
    path: "/droplist/:brand",
    name: "DropSchedule",
    component: DropSchedule,
  },
  {
    path: "/droplist/:brand/:week",
    name: "FullList",
    component: FullList,
  },
  {
    path: "/shop",
    name: "Shop",
    component: Shop,
  },
  {
    path: "/tag",
    name: "Error",
    component: Error,
  },
  {
    path: "/tag/:tag",
    name: "Tag",
    component: Tag,
  },
  {
    path: "/comment",
    component: Comments,
  },
]

const router = new VueRouter({
  routes,
})

export default router
